@import url('https://fonts.googleapis.com/css?family=Space+Mono|PT+Serif:400,400i');
html,
body {
    background: #F5F5F5;
    font-family: "Space Mono", -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
}

.wordCard {
    border: 0;
    padding: 1rem;
}

.wordCard__word {
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-size: 32px;
}

.wordCard__sentence {
    font-family: 'PT Serif', serif;
    font-weight: 400;
    font-style: italic;
}

.fa {
    font-size: 1em;
    margin: 0px 4px;
    width: 12px;
}

.hover {
    cursor: pointer;
}

.word_input {
    border: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
    border-bottom: 2px solid rgb(180, 180, 255);
    border-radius: 0;
    outline: none;
    caret-color: blue;
}

.wordInput {
    font-size: 2.4em;
    font-family: 'PT Serif';
    text-transform: capitalize;
    -webkit-box-shadow: none;
            box-shadow: none;
    border: none;
    outline: none;
}

.wordInput:focus,
.plain:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
    border: none;
    outline: none;
}

.wordInput::-webkit-input-placeholder {
    color: #cdcdcd;
}

.wordInput:-ms-input-placeholder {
    color: #cdcdcd;
}

.wordInput::-ms-input-placeholder {
    color: #cdcdcd;
}

.wordInput::placeholder {
    color: #cdcdcd;
}

.plain::-webkit-input-placeholder {
    color: #b1b1b1;
}

.plain:-ms-input-placeholder {
    color: #b1b1b1;
}

.plain::-ms-input-placeholder {
    color: #b1b1b1;
}

.plain::placeholder {
    color: #b1b1b1;
}

.plain {
    font-size: 1em;
    font-family: 'Space Mono', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif !important;
    padding-top: 0.7rem;
    -webkit-box-shadow: none;
            box-shadow: none;
    border: none;
    outline: none;
    letter-spacing: 0.1rem;
}

.dropdown:focus {
    -webkit-box-shadow: inset 0 0 0 1px #c2c2c2;
            box-shadow: inset 0 0 0 1px #c2c2c2;
}

.bordered {
    border: 2px solid black;
}

.likeheart {
    color: #c33737;
    cursor: pointer;
}

.ff {
    color: #afafaf;
    cursor: pointer;
}

.fa-angle-double-right {
    font-size: 1.1em;
    -webkit-transform: translateY(1px);
        -ms-transform: translateY(1px);
            transform: translateY(1px);
    cursor: pointer;
}

.ff:hover {
    color: #1f1f1f;
}

.as-items .as-item__navigator {
    position: relative;
    left: 0px;
    top: 0px;
}

.as-item {
    margin-bottom: 1.7rem;
}

.as-items a {
    text-decoration: none;
    color: #c33737;
}

.word_input:focus {
    -webkit-box-shadow: none;
            box-shadow: none;
}

.wordCard a {
    text-decoration: none;
    color: #c33737;
}


